.imgme {
    border: 3px solid white;
    float: left;
}

.img-circle {
    border-radius: 50%;
}

.container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    width: 750px;
}

a {
    color: #428bca;
}

#img {
    vertical-align: middle;
    border: 0;
}

#root {
    margin: 0%;
    display: block;
}

.bg1 {
    background-image: url(ukmpano.jpeg);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: auto 100%;
}

#header {
    width: 100%;
    height: 400px;
    color: white;
    margin-bottom: 40px;
}

#headertext {
    margin-top: 50px;
    margin-left: 220px;
}

#headerblob {
    position: absolute;
    left: 20%;
    top: 165px;
    width: 80%;
}

* {
    box-sizing: border-box;
}

body {
    margin: 0%;
    line-height: 1.42857143;
}

div {
    display: block;
}

html, body {
    height: 100%;
    font-family: 'Roboto', sans-serif;
    color: #444444;
}

#svgico {
    display: inline-block;
}

#htname {
    font-size: 46px;
    font-weight: 400;
}

#htdesc {
    font-size: 18px;
    font-weight: 300;
    margin-top: -6px;
}

#htem {
    font-size: 14px;
    font-weight: 300;
}

hr.soft {
    border: 0;
    height: 1px;
    background-image: -webkit-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
    /* background-image:    -moz-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
    background-image:     -ms-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
    background-image:      -o-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0)); */
    padding: 0;
    margin: 0;
  }